body {
  margin: 0;
  padding: 0;
  background: url("bg01.png") no-repeat center center fixed;
  background-size: cover;
  font-family: "Roboto", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.calculator-container {
  height: 75vh;
  background-color: #070b26; /* Match background of image */
  border-radius: 20px; /* Match rounded corners of calculator */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  width: 300px; /* Adjust the width to fit the buttons */
  display: flex;
  flex-direction: column; /* Stack display and buttons vertically */
  align-items: center; /* Center align content horizontally */
}

.display {
  height: 30vh;
  display: flex;
  flex-direction: column; /* Display input and output in separate lines */
  align-items: flex-end;
  font-size: 32px; /* Increase the size of the display */
  background-color: #172d67; /* Slightly darker background for display */
  padding: 10px; /* Add padding to display */
  width: 100%; /* Display takes 100% width of container */
  box-sizing: border-box; /* Include padding in width */
  border-radius: 20px 20px 0 0;
  position: relative; /* Enable positioning */
}

input[type="text"] {
  height: 10vh;
  font-size: 32px; /* Increase the size of the display */
  border: none;
  outline: none;
  width: 75%; /* Input takes 100% width of display */
  background-color: transparent; /* Transparent background for input */
  color: #fff; /* Text color for input */
  text-align: right;
  position: relative; /* Enable positioning */
}

.clear-button {
  background-color: #172d67; /* Red background color */
  color: #fff; /* Text color */
  border: none;
  border-radius: 50%; /* Make it round */
  width: 25px; /* Adjust the width as needed */
  height: 25px; /* Adjust the height as needed */
  font-size: 25px; /* Adjust the font size as needed */
  position: absolute; /* Position absolutely within the .display */
  left: 30px; /* Adjust the left position to control the distance from the left edge */
  top: 10%; /* Position vertically at the center */
  transform: translateY(-50%); /* Center vertically */
  cursor: pointer;
}

/* ... (rest of the CSS code) */

.result {
  font-size: 32px;
  color: #fff; /* Text color for result */
  margin-top: 5px; /* Add spacing between input and output */
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* Align buttons in a 4x4 grid */
  gap: 0; /* Remove spacing between buttons and rows */
  width: 100%; /* Buttons take 100% width of container */
}

button {
  width: 100%;
  height: 60px; /* Increase the height of the buttons */
  font-size: 24px; /* Adjust the font size of the buttons */
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #070b26; /* Dark gray shade for buttons */
  color: #fff; /* Text color for buttons */
  transition: background-color 0.2s ease-in-out; /* Smooth hover effect */
  margin: 0; /* Remove margins */
}
.operators {
  color: #475387;
}
.click:hover {
  background-color: #000; /* Darker shade on hover */
}
.clear-button:hover {
  color: #475387;
}
.buttons .row {
  display: grid;
  grid-template-columns: repeat(
    auto-fill,
    minmax(75px, 1fr)
  ); 
}